import { createGlobalStyle } from "styled-components";
import theme from "../global/theme";

const GlobalStyle = createGlobalStyle`
html {
  height: 100%;
  width: 100%;
  
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  overflow-x: hidden;
  overflow-y: auto;
  padding: 0 5rem 0 0 ;
  /*  fixed   body    */
  margin: 0;
  word-break: keep-all; //   
  /* word-break: break-all; //    */
  
  
  /*   */
  font-family: "Kr";
  line-height: 30px;
  font-size: 1.1rem;
  color: ${theme.Black};
  
}

#wrap {
  height: 100%;
  width: 100%;
  min-width: 770px;
  box-sizing: border-box;


  .body-container > div {
  
  margin: 0;
}
}


`;

export default GlobalStyle;
