import { useEffect, useRef } from "react";
import { Link, useNavigate } from "react-router-dom";
import Slider from "react-slick";
import styled from "styled-components";
import theme from "../global/theme";
import { Wrapper, Header, Button } from "../global/MyStyles";

const Projects = () => {
  const navigate = useNavigate();
  const sliderRef = useRef(null);

  useEffect(() => {
    const handleSlider = (e) => {
      //     &&      
      if (e.deltaY > 0 && sliderRef.current) {
        sliderRef.current.slickNext();
      } else if (e.deltaY < 0 && sliderRef.current) {
        sliderRef.current.slickPrev();
      }
    };

    //     
    document.addEventListener("wheel", handleSlider);

    //      
    return () => {
      document.removeEventListener("wheel", handleSlider);
    };
  }, [sliderRef.current]);

  const settings = {
    dots: false,
    infinite: false,
    speed: 400,
    slidesToShow: 2,
    slidesToScroll: 1,
    swipeToSlide: true,
    vertical: false,
    verticalSwiping: false,
  };

  const projectList = [
    {
      id: 1,
      type: " ",
      img: require("../img/MOTI.jpg"),
      name: "MOTI",
      title: " , !",
      description:
        "  ,           .",
      duration: "2022.11.16 ~ 2023.01.06",
      durationDetail: "2022 11 16 ~ 2023 01 06",
      language: "React, JavaScript, HTML, CSS ",
      link: "https://project-moti.netlify.app/",
      taskList: [
        {
          id: 1,
          skill: "Redux",
          context:
            "Redux        ",
        },
        {
          id: 2,
          skill: "React-persist",
          context:
            "local storage  ,          ",
        },
        {
          id: 3,
          skill: "React-router",
          context:
            " SPA(Single Page Application)        <Outlet/>      ",
        },

        {
          id: 4,
          skill: "React-slick",
          context:
            "React-slick        ",
        },
        {
          id: 6,
          skill: "CSS",
          context:
            "flex / grid            ",
        },
      ],
    },
    {
      id: 2,
      type: " ",
      img: require("../img/resume.jpg"),
      name: "RESUME",
      title: " ?",
      description:
        "  .    .",
      duration: "2023.03.20 ~ 2023.04.25",
      durationDetail: "2023 03 20 ~ 2023 04 25",
      language: "React, JavaScript, HTML, CSS, styled-components ",
      link: "https://zionimo.github.io/resume/",
      taskList: [
        {
          id: 1,
          skill: "React-router",
          context:
            " SPA(Single Page Application)        <Outlet/>      ",
        },
        {
          id: 2,
          skill: "React-slick",
          context:
            "      map()          ",
        },
        {
          id: 3,
          skill: "MUI",
          context:
            "        MUI     ",
        },
        {
          id: 4,
          skill: "styled-componets",
          context:
            "         . CSS-in-JS                 ",
        },
      ],
    },
    {
      id: 3,
      type: " ",
      img: require("../img/bookggureomi.jpg"),
      name: "BOOK GGUREOMI",
      title: "  ! ",
      description:
        "   ''     . React , RestAPI      .   !",
      duration: "2023.03 ~",
      durationDetail: "",
      language: "",
      link: "",
      taskList: [],
    },
  ];

  const goToDetail = (projectId) => {
    if (projectId < 3) {
      navigate(`/projects/${projectId}`, { state: projectList });
    }
  };

  return (
    <ProjectWrapper>
      <div>
        <Header>PROJECTS</Header>
      </div>

      <Slider {...settings} ref={sliderRef}>
        {projectList.map((project, index) => (
          <div key={index}>
            <ImageWrapper
              onClick={() => goToDetail(project.id)}
              style={{ cursor: project.id < 3 ? "pointer" : "default" }}
            >
              <Image src={project.img} alt="index" />
              {project.id < 3 ? (
                <ImageText>
                  <span>{project.name}</span>
                  <span>{project.duration}</span>
                </ImageText>
              ) : (
                <ImageText>
                  <span>{project.name}</span>
                  <span>  .</span>
                </ImageText>
              )}
            </ImageWrapper>

            <div>
              <Name>{project.name}</Name>
              <Title>{project.title}</Title>
            </div>

            <ContextWrapper>
              <span>{project.description}</span>
              <span>{project.duration}</span>
            </ContextWrapper>

            {/*      */}
            {project.id < 3 ? (
              <Button>
                <Link to={"/projects/" + project.id} state={projectList}>
                  More View
                </Link>
              </Button>
            ) : undefined}
          </div>
        ))}
      </Slider>
    </ProjectWrapper>
  );
};

export default Projects;

const ProjectWrapper = styled(Wrapper)`

  .slick-slide {
    /*     */
    padding-right: 60px;
  }
`;

const ImageWrapper = styled.div`
  position: relative;
  width: 100%;
  height: auto;
`;

const Image = styled.img`
  max-width: 100%;
  margin-bottom: 15px;
`;

const ImageText = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;

  display: flex;
  flex-direction: column;
  justify-content: center; //  
  align-items: center; //   

  > * {
    color: white;
    opacity: 0;
    transform: translateY(-20px); // hover  20px  
    transition: opacity 0.4s, transform 0.3s;
  }

  span:first-child {
    //    
    font-family: "sub En";
    font-weight: bold;
    font-size: 2rem;
    margin-bottom: 8px;
  }

  :hover {
    background-color: #000000bb; //    
    > * {
      opacity: 1;
      transform: translateY(0); /*  10px     */
    }
  }
`;

const Name = styled.p`
  font-family: "sub En";
  font-size: 2.1rem;
  font-weight: bold;
  margin: 0 0 15px 0;
`;

const Title = styled.p`
  font-size: 1.3rem;
  font-weight: bold;
  margin: 0 0 15px 0;
`;

const ContextWrapper = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;

  span:first-child {
    height: 90px; // &   
  }

  span:last-child {
    font-size: 1rem; //   
  }
`;
