import { useState } from "react";
import { useLocation, useParams } from "react-router-dom";
import theme from "../global/theme";
import styled from "styled-components";
import List from "@mui/material/List";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import Collapse from "@mui/material/Collapse";
import ExpandLess from "@mui/icons-material/ExpandLess";
import ExpandMore from "@mui/icons-material/ExpandMore";
import { Wrapper } from "../global/MyStyles";

const ProjectDetail = () => {
  const location = useLocation();
  const projectList = location.state; // Projects
  const { id } = useParams(); // url name 
  const project = projectList.find((p) => p.id == id); // ProjectList url name  find()  

  const [openId, setOpenId] = useState(false);

  const {
    type,
    name,
    title,
    description,
    durationDetail,
    link,
    language,
    taskList,
  } = project; //   

  //   /  
  // handleClick   , id   null ,  id 
  const handleClick = (id) => {
    setOpenId(openId === id ? null : id);
  };

  return (
    <Wrapper>
      <InfoWrapper>
        <Name>{name}</Name>
        <Title>{title}</Title>
        <Description>{description}</Description>
      </InfoWrapper>

      <Article>
        <Term></Term>
        <dd>{type}</dd>

        <Term></Term>
        <dd>
          <SiteLink href={link} target="_blank" rel="noopener noreferrer">
             !
          </SiteLink>
        </dd>

        <Term> </Term>
        <dd>{durationDetail}</dd>

        <Term> </Term>
        <dd>{language}</dd>

        <Term> </Term>
        <dd>
          {/* mui Nasted List  */}
          {taskList.map((task) => (
            <List
              key={task.id}
              sx={{
                width: "100%",
                maxWidth: 600,
                bgcolor: "background.paper",
                boxShadow: "0px 0px 4px rgba(0, 0, 0, 0.3)",
                padding: "0px 0px 0px 0px",
                margin: "4px 0px",
              }}
              component="nav"
              aria-labelledby="nested-list-subheader"
            >
              <ListItemButton onClick={() => handleClick(task.id)}>
                <ListItemText primary={task.skill} />
                {openId === task.id ? <ExpandLess /> : <ExpandMore />}
              </ListItemButton>
              <Collapse in={openId === task.id} timeout="auto" unmountOnExit>
                <List component="div" disablePadding>
                  <ListItemText sx={{ pl: 2, margin: "20px 0px" }}>
                    <ListItemText primary={task.context} />
                  </ListItemText>
                </List>
              </Collapse>
            </List>
          ))}
        </dd>
      </Article>
    </Wrapper>
  );
};

export default ProjectDetail;

// const Wrapper = styled.div`
//   overflow-y: auto;
//   width: 100vw;
//   height: 100vh;
//   padding: 0px;
//   margin: 0px;
//   box-sizing: border-box;
// `;

const InfoWrapper = styled.div`
  width: 100%;
  padding: 50px 0 50px 0;
  margin: 0 0 60px 0;
  border-top: 2px solid ${theme.SubTitle};
  border-bottom: 2px solid ${theme.SubTitle};
`;

const Name = styled.p`
  font-family: "sub En";
  font-size: 3rem;
  font-weight: bold;
  margin: 0 0 20px 0;
`;

const Title = styled.p`
  font-size: 1.3rem;
  margin: 0 0 50px 0;
  font-weight: bold;
`;

const Description = styled.p`
  margin: 0;
`;

const Article = styled.dl`
  display: grid;
  grid-template-columns: 0.3fr 2fr;

  > * {
    margin: 0 0 20px 0;
  }
`;

const Term = styled.dt`
  color: ${theme.SubTitle};
`;

const SiteLink = styled.a`
  font-weight: bold;
  color: ${theme.Title};

  :hover {
    color: ${theme.Point};
  }
`;
